    position: relative;
    top: -23px;
    left: 0;
    right: 0;
    z-index: 100; }
  @media screen and (min-width: 48em) {
    .user-info {
      position: static;
      display: block;
      padding-bottom: 20px;
      padding-top: 0; } }
  .user-info__table {
    width: auto;
    margin-bottom: 8px;
    font-weight: 300; }
    @media screen and (min-width: 48em) {
